'use strict';

(function (factory) {

    "use strict";

    if (typeof define === 'function' && define['amd']) {
        define(['knockout', 'text!./view.html', 'app/data-context-blog', 'lib/auth', 'lib/knockout-bindings-highlight', 'lib/knockout-bindings-ext', 'lib/knockout-bindings-datetime'], factory);
    }
})(function (ko, template, ctx, auth) {

    "use strict";

    function ViewModel(params) {

        var self = this;

        self.loaded = ko.observable(false);
        self.model = ko.observable();
        self.notFound = ko.observable();
        self.authenticated = ko.observable(auth.authenticated());

        ctx.blogs.getById(params.id).done(function (data) {

            if (data) {
                self.model(data);
            } else {
                self.notFound(true);
            }

            self.loaded(true);
        });
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});